{/* DRAWER IMPLEMENTATION */}
<Drawer>
  <DrawerTrigger asChild>
    <Button
      className="w-full group-hover:bg-primary/90 transition-all text-base font-medium"
      variant="outline"
    >
      View Details
      <ArrowRight className="ml-2 h-4 w-4 transition-transform group-hover:translate-x-1" />
    </Button>
  </DrawerTrigger>

  {/* Changed: Added fixed height h-[85vh] and flex-col to stop main window scrolling */}
  <DrawerContent className="h-[85vh] max-h-[85vh] flex flex-col fixed bottom-0 left-0 right-0 outline-none rounded-t-[20px]">
    
    {/* Header - Fixed at Top (shrink-0) */}
    <DrawerHeader className="text-left border-b border-border/10 pb-4 shrink-0">
      <div className="flex items-center justify-between">
        <div className="flex items-center gap-3">
          <div className="h-10 w-10 bg-primary/10 rounded-full flex items-center justify-center text-primary">
            <Icon className="h-5 w-5" />
          </div>
          <DrawerTitle className="text-2xl font-bold">
            {service.title}
          </DrawerTitle>
        </div>
        <DrawerClose asChild>
          <Button
            variant="ghost"
            size="icon"
            className="h-8 w-8 rounded-full"
          >
            <X className="h-4 w-4" />
          </Button>
        </DrawerClose>
      </div>
      <DrawerDescription className="mt-2 text-base">
        Overview of the {service.title} package.
      </DrawerDescription>
    </DrawerHeader>

    {/* Body - Fixed Layout (overflow-hidden wrapper) */}
    <div className="flex-1 flex flex-col md:flex-row gap-6 p-4 md:p-6 overflow-hidden">
      
      {/* Left: Image Area 
          Mobile: Fixed height (h-48), fits nicely at top.
          Desktop: 40% width, full height of the body, object-cover fits perfectly.
      */}
      <div className="w-full md:w-2/5 shrink-0 h-48 md:h-full rounded-xl overflow-hidden shadow-sm border border-border/50 relative">
        <img
          src={service.image}
          alt={service.title}
          className="w-full h-full object-cover"
        />
        {/* Optional: Subtle gradient overlay on image for better look */}
        <div className="absolute inset-0 bg-black/10" />
      </div>

      {/* Right: Content Area 
          overflow-y-auto allows ONLY text to scroll if it gets too long, 
          keeping the image and footer static.
      */}
      <div className="flex-1 flex flex-col gap-6 overflow-y-auto pr-2">
        <div>
          <h4 className="text-lg font-semibold text-foreground mb-2 sticky top-0 bg-background/95 backdrop-blur py-1 z-10">
            Description
          </h4>
          <p className="text-muted-foreground leading-relaxed text-sm md:text-base">
            {service.longDescription}
          </p>
        </div>

        <div>
          <h4 className="text-lg font-semibold text-foreground mb-3 flex items-center gap-2 sticky top-0 bg-background/95 backdrop-blur py-1 z-10">
            <CheckCircle className="h-5 w-5 text-primary" />
            What's Included?
          </h4>
          <div className="grid grid-cols-1 gap-2">
            {service.includes.map((item, idx) => (
              <div
                key={idx}
                className="flex items-start gap-3 p-2.5 rounded-lg bg-muted/40 border border-border/40 hover:bg-muted/60 transition-colors"
              >
                <Check className="h-4 w-4 text-primary mt-0.5 shrink-0" />
                <span className="text-sm text-foreground/90 font-medium">
                  {item}
                </span>
              </div>
            ))}
          </div>
        </div>
      </div>
    </div>

    {/* Footer - Fixed at Bottom (shrink-0) */}
    <DrawerFooter className="border-t border-border/10 pt-4 shrink-0 bg-background pb-8 md:pb-4">
      <div className="flex flex-col sm:flex-row gap-3 w-full max-w-4xl mx-auto">
        <DrawerClose asChild>
          <Button variant="outline" className="flex-1 sm:flex-none">
            Close
          </Button>
        </DrawerClose>
        <Link href={service.url} className="flex-1">
          <Button className="w-full text-base font-semibold" size="lg">
            Book {service.title} Now
          </Button>
        </Link>
      </div>
    </DrawerFooter>
  </DrawerContent>
</Drawer>